<template>
	<!--客户角色管理-->
	<div class="d-box v-box">
		<div class="headDiv">
			<div class="iconButton clearfix">
				<Button :type='edit.type' :icon='edit.icon' :text='edit.text' @click="editClick"></Button>
			</div>
		</div>

		<div class="flexTable">
			<div class="d-box v-box">
				<div class="flexTable">
					<div class="tableDiv">
						<!--3、主内容-->
						<el-table v-loading="loading" ref="multipleTable" stripe :data="rows" tooltip-effect="dark" style="width: 100%;padding:0 20px;" height="100%" @selection-change="handleSelectionChange" @row-click="toggleSelection">
							<el-table-column type="selection" width="55" align="center"></el-table-column>
							<el-table-column prop="id" label="ID" align="center">
							</el-table-column>
							<el-table-column prop="roleName" label="角色" align="center">
							</el-table-column>
							<!--isShowEta-->
							<el-table-column label="显示预计到达时间" align="center">
								<template slot-scope="scope">
									<span style="margin-left: 10px">{{scope.row.isShowEta?'是' : '否'}}</span>
								</template>
							</el-table-column>
							<el-table-column label="显示最新地理位置" align="center">
								<template slot-scope="scope">
									<span style="margin-left: 10px">{{scope.row.isShowLocation?'是' : '否'}}</span>
								</template>
							</el-table-column>
						</el-table>
					</div>

				</div>
				<div class="footerDiv">
					<!--4、底部-->
					<el-pagination style="text-align: center;padding: 6px 0px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageRows" layout="total,sizes, prev,pager, next,jumper" :total="pageTotal">
					</el-pagination>
				</div>
			</div>
		</div>

		<!--2、弹窗-->
		<el-dialog :title="popwindow.title" :visible.sync="popwindow.dialogFormVisible" center>
			<el-form :model="popwindow">
				<el-form-item :label-position="popwindow.labelPosition" label="角色" :label-width="popwindow.formLabelWidth">
					<el-select disabled v-model="popwindow.roleName" filterable placeholder="请选择角色" @change="selectchangeClick">
						<el-option v-for="item in popwindow.roleNameList" :key="item.id" :label="item.name" :value="item.name">
						</el-option>
					</el-select>
				</el-form-item>
				<el-checkbox-group v-model="popwindow.checkList">
					<el-checkbox label="显示预计到达时间"></el-checkbox>
					<el-checkbox label="显示最新地理位置"></el-checkbox>
				</el-checkbox-group>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<SolidButton :type='"hollow"' :text='"取消"' @click="popwindow.dialogFormVisible = false"></SolidButton>
				<SolidButton :type='"solid"' :text='"确认"' @click="popSureClick"></SolidButton>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import Button from '../../button/Button.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';
	export default {
		components: {
			Button,
			SolidButton,
		},
		data() {
			return {
				//组件化
				edit: {
					text: '编辑',
					type: 'icon-edit',
					icon: 'icon-edit',
				},
				//2、弹窗
				popwindow: {
					//基本配置
					dialogFormVisible: false,
					labelPosition: 'left',
					formLabelWidth: '40px',
					title: '',
					//区分
					//界面参数
					roleName: '',
					roleNameId: '',
					checkList: [],
					isShowEta: 0,
					isShowLocation: 0,
					//所有rolename组成的数组
					roleNameList: [{
						name: "安吉物流",
						id: 0
					}, {
						name: "主机厂",
						id: 1
					}, {
						name: "经销商",
						id: 2
					}],
				},
				loading:true,
				//3、main内容
				rows: [],
				//列表选择的数组（上面是list的所有，下面是for循环后id的合集）
				multipleSelection: [], //勾选的数组

				//分页
				currentPage: 1, //当前页数
				pageRows: 10, //每页
				pageTotal: 0, //总数
			}
		},
		methods: {
			loadData() {
				var self = this;
				var p = {
					pageNum: self.currentPage,
					pageRows: self.pageRows
				}
				//1、列表接口
				self.$api.post('/crm/role/doCustomerRoleList', p, r => {
					console.log(r);
					self.loading = false;
					self.rows = r.repData
					self.pageTotal = r.repData.length
				}, e => {
					self.loading = false;
					this.$message.error(e);
				})
			},
			//1、head头部
			editClick() {
				var self = this;
				if(self.multipleSelection.length == 1) {
					//数据清空
					self.popwindow.checkList = []
					//基础数据
					self.popwindow.title = "编辑"
					self.popwindow.dialogFormVisible = true
					self.popwindow.roleName = self.multipleSelection[0].roleName
					self.popwindow.roleNameId = self.multipleSelection[0].id
					//
					if(self.multipleSelection[0].isShowEta == 1) {
						self.popwindow.checkList.push('显示预计到达时间')
					}
					if(self.multipleSelection[0].isShowLocation == 1) {
						self.popwindow.checkList.push('显示最新地理位置')
					}
				} else {
					this.$message({
						message: '请勾选单条数据',
						type: 'warning'
					});
				}
			},
			//2、窗口
			selectchangeClick() {
				var self = this;
				self.popwindow.roleNameId = self.popwindow.roleName
			},
			popSureClick() {
				//编辑接口
				var self = this;
				self.popwindow.isShowEta = 0;
				self.popwindow.isShowLocation = 0;
				console.log(self.popwindow.checkList)
				for(var i = 0; i < self.popwindow.checkList.length; i++) {
					if(self.popwindow.checkList[i] == "显示预计到达时间") {
						self.popwindow.isShowEta = 1
					}
					if(self.popwindow.checkList[i] == '显示最新地理位置') {
						self.popwindow.isShowLocation = 1
					}
				}
				console.log(self.popwindow.isShowEta, self.popwindow.isShowLocation)
				var p = {
					id: self.multipleSelection[0].id,
					roleName: self.popwindow.roleName,
					isShowEta: self.popwindow.isShowEta,
					isShowLocation: self.popwindow.isShowLocation
				}
				self.$api.post('/crm/role/doCustomerRoleUpdate ', p, r => {
					console.log(r);
					self.loadData();
					self.popwindow.dialogFormVisible = false;
				}, e => {
					this.$message.error(e);
				})
			},
			//3、main内容
			//select行改变
			handleSelectionChange(val) {
				this.multipleSelection = val;
				console.log(val)
			},
			//点击行选中复选框
			toggleSelection(row) {
				var rows = [];
				rows.push(row);
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			//4、footer分页
			//每页多少条数据
			handleSizeChange(val) {
				var self = this;
				console.log("每页" + val);
				self.pageRows = val;
				self.loadData();
			},
			//当前第几页
			handleCurrentChange(val) {
				var self = this;
				console.log("当前页" + val);
				self.currentPage = val;
				self.loadData();
			}
		},
		created: function() {
			this.loadData();
		},
	}
</script>

<style scoped="scoped">
	/*整体flex布局*/
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.headDiv {
		padding: 20px;
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
	/*head按钮*/
	
	.iconButton {
		float: left;
		padding-top: 20px;
	}
	/*3、窗口*/
	
	.el-form:after {
		content: "";
		display: block;
		clear: both;
	}
</style>


